import { motion } from 'framer-motion';

export default function GlobalBackground() {
  return (
    <div className="fixed inset-0 -z-10 overflow-hidden pointer-events-none">
      {Array.from({ length: 20 }).map((_, i) => (
        <motion.div
          key={i}
          className={`absolute rounded-full ${
            i % 3 === 0 ? 'bg-orange-400' : i % 3 === 1 ? 'bg-yellow-300' : 'bg-pink-400'
          } blur-sm opacity-80`}
          style={{
            width: `${Math.random() * 5 + 4}px`, // 4–9px
            height: `${Math.random() * 5 + 4}px`,
            top: `${Math.random() * 100}%`,
            left: '-20px',
          }}
          animate={{
            x: '120vw',
            opacity: [0, 1, 1, 0],
          }}
          transition={{
            repeat: Infinity,
            duration: Math.random() * 6 + 5, // 5–11s
            ease: 'linear',
            delay: Math.random() * 4,
          }}
        />
      ))}
    </div>
  );
}
